<template>
<div id="publication-list">
    <div class="publication_fig">
        <img :src="getImageUrl(item.figname)" width="100%">
    </div>
    <div class="publication_description">
        <h1 @click="onClick" @mouseover="active=true" @mouseleave="active=false">
            {{item.name}}
        </h1>
        <span v-for="(i, index) in item.authors" :key="index">
            <span v-if="i === 'Man Ho Lam'"><strong>{{ i }}</strong></span>
            <span v-else>{{ i }}</span>
            <span v-if="index < item.authors.length - 1">, </span>
        </span>
        <h2 v-html="item.conference"></h2>
        <div class="resources-container">
            <a v-if="item.websiteLink" :href="item.websiteLink" rel="noopener noreferrer"><i class="fas fa-globe"></i>Website</a>
            <a v-if="item.conference" :href="item.paperLink" rel="noopener noreferrer"><i class="ai ai-arxiv"></i>{{ item.paper }}</a>
            <a v-if="item.codeLink" :href="item.codeLink" rel="noopener noreferrer"><i class="fab fa-github"></i>Code</a>
        </div>
    </div>
</div>
</template>

<script>
export default {
    name: 'PublicationItem',
    props: ['item', 'index', 'itemStatus', 'switchStatus'],
    data() {
        return {
            height: 1000,
            collapse: true,
            active: false,
            status: 'collapsed'
        }
    },
    methods: {
        onClick(){
            this.active = true
            this.switchStatus(this.index)
        },
        getImageUrl(figname) {
            return require(`@/assets/publication//${figname}`);
        },
    }
}
</script>

